'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { 
    LayoutDashboard, 
    Users, 
    ShoppingBag, 
    Utensils, 
    CalendarDays, 
    Wallet, 
    Settings, 
    LogOut,
    Box,
    ChevronDown, 
    ChevronRight,
    Tags,
    Scale,
    Truck,
    ShoppingCart,
    Info ,
    BookText
} from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';

type SubMenuItem = {
    title: string;
    href: string;
    icon?: any;
};

type MenuItem = {
    title: string;
    href?: string; 
    icon: any;
    submenu?: SubMenuItem[];
};

const menuItems: MenuItem[] = [
    { title: 'Dashboard', href: '/admin/dashboard', icon: LayoutDashboard },
    
    { 
        title: 'Data Master', 
        icon: Settings,
        submenu: [
            { title: 'Kategori', href: '/admin/master/kategori', icon: Tags },
            { title: 'Satuan Unit', href: '/admin/master/satuan', icon: Scale },
            { title: 'Data Lapangan', href: '/admin/lapangan', icon: Box },
            { title: 'Data Supplier', href: '/admin/master/supplier', icon: Truck }, 
            { title: 'Metode Pembayaran', href: '/admin/master/payment-method', icon: Wallet },
            { title: 'Status Booking', href: '/admin/master/status-booking', icon: Info },
            { title: 'Jenis Transaksi', href: '/admin/master/jenis-transaksi', icon: BookText },
        ]
    },

    { title: 'Booking Lapangan', href: '/admin/bookings', icon: CalendarDays },
    { title: 'Produk & Menu', href: '/admin/produk-resto', icon: Utensils },


    { 
        title: 'Inventaris & Stok', 
        icon: ShoppingBag,
        submenu: [
            { title: 'Stok Bahan Baku', href: '/admin/inventaris', icon: Box }, 
            { title: 'Pembelian Stok', href: '/admin/inventaris/pembelian', icon: ShoppingCart }, 
        ]
    },
    
    { title: 'Users & Role', href: '/admin/users', icon: Users },
    { title: 'Laporan Keuangan', href: '/admin/keuangan', icon: Wallet },
];

export default function Sidebar() {
    const pathname = usePathname();
    const { logout } = useAuth();

    const [expandedMenus, setExpandedMenus] = useState<string[]>([]);

    useEffect(() => {
        menuItems.forEach(item => {
            if (item.submenu) {
                const isChildActive = item.submenu.some(sub => pathname.startsWith(sub.href));
                if (isChildActive) {
                    setExpandedMenus(prev => prev.includes(item.title) ? prev : [...prev, item.title]);
                }
            }
        });
    }, [pathname]);

    const toggleMenu = (title: string) => {
        setExpandedMenus(prev => 
            prev.includes(title) 
                ? prev.filter(t => t !== title) 
                : [...prev, title] 
        );
    };

    return (
        <aside className="w-64 bg-white border-r border-gray-200 hidden md:flex flex-col h-screen fixed left-0 top-0 z-20">
            <div className="h-16 flex items-center justify-center border-b border-gray-200">
                <h1 className="text-2xl font-bold text-gray-900">
                    MainDi<span className="text-[#D93F21]">Hatta</span>
                </h1>
            </div>

            <nav className="flex-1 overflow-y-auto py-4 px-3 space-y-1 custom-scrollbar">
                {menuItems.map((item) => {
                    
                    if (!item.submenu) {
                        const isActive = pathname === item.href || pathname.startsWith(`${item.href}/`);
                        return (
                            <Link
                                key={item.title}
                                href={item.href!}
                                className={`flex items-center px-4 py-3 text-sm font-medium rounded-lg transition-all duration-200 group ${
                                    isActive
                                        ? 'bg-red-50 text-[#D93F21]'
                                        : 'text-gray-600 hover:bg-gray-50 hover:text-gray-900'
                                }`}
                            >
                                <item.icon 
                                    className={`w-5 h-5 mr-3 transition-colors ${
                                        isActive ? 'text-[#D93F21]' : 'text-gray-400 group-hover:text-gray-600'
                                    }`} 
                                />
                                {item.title}
                            </Link>
                        );
                    }

                    const isExpanded = expandedMenus.includes(item.title);
                    const isChildActive = item.submenu.some(sub => pathname.startsWith(sub.href));

                    return (
                        <div key={item.title} className="space-y-1">
                            <button
                                onClick={() => toggleMenu(item.title)}
                                className={`w-full flex items-center justify-between px-4 py-3 text-sm font-medium rounded-lg transition-colors ${
                                    isChildActive 
                                        ? 'text-gray-900 bg-gray-50' 
                                        : 'text-gray-600 hover:bg-gray-50 hover:text-gray-900'
                                }`}
                            >
                                <div className="flex items-center">
                                    <item.icon 
                                        className={`w-5 h-5 mr-3 ${
                                            isChildActive ? 'text-[#D93F21]' : 'text-gray-400'
                                        }`} 
                                    />
                                    {item.title}
                                </div>
                                {isExpanded ? (
                                    <ChevronDown className="w-4 h-4 text-gray-400" />
                                ) : (
                                    <ChevronRight className="w-4 h-4 text-gray-400" />
                                )}
                            </button>

                            {/* SUBMENU ITEMS */}
                            {isExpanded && (
                                <div className="space-y-1 pl-4 relative before:absolute before:left-6 before:top-0 before:bottom-0 before:w-px before:bg-gray-200">
                                    {item.submenu.map((subItem) => {
                                        let isSubActive = pathname === subItem.href || pathname.startsWith(`${subItem.href}/`);

                                        if (isSubActive && item.submenu) {
                                            const betterMatch = item.submenu.find(sibling => 
                                                sibling.href !== subItem.href && 
                                                sibling.href.length > subItem.href.length && 
                                                (pathname === sibling.href || pathname.startsWith(`${sibling.href}/`)) 
                                            );
                                            
                                            if (betterMatch) {
                                                isSubActive = false;
                                            }
                                        }

                                        return (
                                            <Link
                                                key={subItem.href}
                                                href={subItem.href}
                                                className={`flex items-center px-4 py-2 text-sm font-medium rounded-lg transition-colors relative z-10 ${
                                                    isSubActive
                                                        ? 'text-[#D93F21] bg-red-50'
                                                        : 'text-gray-500 hover:text-gray-900 hover:bg-gray-50'
                                                }`}
                                            >
                                                {subItem.icon && (
                                                    <subItem.icon className={`w-4 h-4 mr-2 ${isSubActive ? 'text-[#D93F21]' : 'text-gray-400'}`} />
                                                )}
                                                {subItem.title}
                                            </Link>
                                        );
                                    })}
                                </div>
                            )}
                        </div>
                    );
                })}
            </nav>

            <div className="p-4 border-t border-gray-200">
                <button 
                    onClick={logout}
                    className="flex items-center w-full px-4 py-2 text-sm font-medium text-red-600 bg-red-50 rounded-lg hover:bg-red-100 transition-colors"
                >
                    <LogOut className="w-5 h-5 mr-3" />
                    Logout
                </button>
            </div>
        </aside>
    );
}